import FileDownloadOutlinedIcon from '@mui/icons-material/FileDownloadOutlined';
import makeStyles from '@mui/styles/makeStyles';
import React from 'react';
import { getLocaleDate, pdfmakedownload } from '../utils/utils';
import MyButton from './UI/MyButton';

const useStyles = makeStyles(theme => ({
    icon: {
        margin: theme.spacing(0.5, 1, 0.5, 0),
        fontSize: '1.2rem'
    },
    iconWrapper: {
        display: 'flex',
        alignItems: 'center',
        width: '100%',
        height: '100%'
    },
}))

const ExportFile = ({ data , button }) => {
    const classes = useStyles();

    var dd = {
        content: [
            {
                columns: [
                    {
                        text: [
                            'NG B I HC CN TH \n',
                            'CHI B KHOA CNTT&TT'
                        ]
                    },
                    [
                        {
                            text: 'NG CNG SN VIT NAM \n',
                            alignment: 'center'
                        },
                        {
                            text: `Ninh Kiu, ngy ${(new Date()).getDate()} thng ${(new Date()).getMonth() + 1} nm ${(new Date()).getFullYear()} \n`,
                            alignment: 'right'
                        }
                    ]
                ],
            },
            {
                text: 'S YU L LCH NG VIN \n',
                alignment: 'center',
                style: 'header',
                bold: true,
                margin: [0, 24, 0, 0]
            },
            {
                style: 'content',
                columns: [
                    {
                        style: 'tableExample',
                        table: {
                            widths: [80],
                            heights: [106.66667],
                            body: [['']]
                        },
                        width: 120
                    },
                    {
                        columns: [
                            {
                                text: [
                                    `H tn: ${data.HoTen} \n\n`,
                                    `Ngy sinh: ${getLocaleDate(data.NgaySinh)} \n\n`,
                                    `CMND: ${data.CMND} \n\n`,
                                    `M s ng vin: ${data.MaSoDangVien} \n\n`,
                                    `Dn tc: ${data.TenDanToc} \n\n`,
                                ],
                            },
                            {
                                text: [
                                    `Gii tnh: ${data.TenGioiTinh} \n\n`,
                                    `Ni sinh: ${data.NoiSinh} \n\n`,
                                    `Quc tch: ${data.QuocTich} \n\n`,
                                    `Tn gio: ${data.TenTonGiao} \n\n`,
                                    `Chi b: ${data.TenChiBo} \n\n`,
                                ],
                            }
                        ],

                        widths: '*',
                        margin: [0, 5, 0, 0]
                    }
                ]
            },
            {
                text: [
                    `Qu qun: ${data.QueQuan} \n\n`,
                    `a ch thng tr: ${data.DiaChiThuongTru} \n\n`,
                    `Ni  hin ti: ${data.NoiOHienTai} \n\n`,
                ]
            },
            {
                columns: [
                    {
                        text: [
                            `Chi b: ${data.TenChiBo} \n\n`,
                            `Trnh  tin hc: ${data.TenTinHoc} \n\n`,
                        ],
                    },
                    {
                        text: [
                            `Trnh  hc vn: ${data.TrinhDoHocVan} \n\n`,
                            `Trnh  chnh tr: ${data.TenChinhTri} \n\n`,
                        ],
                        width: 210
                    }
                ],

            },
            {
                text: `Ngoi ng - Trnh : ${data.NgoaiNguTrinhDo} \n\n`
            },
            {
                columns: [
                    {
                        text: [
                            `Ngy vo on: ${getLocaleDate(data.NgayVaoDoan)} \n\n`,
                            `Ngy vo ng ln u: ${getLocaleDate(data.NgayVaoDang)} \n\n`,
                            `Ngy vo ng chnh thc: ${getLocaleDate(data.NgayChinhThuc)} \n\n`,
                            `S th: ${data.SoThe} \n\n`,
                        ],
                    },
                    {
                        text: [
                            `Ni vo on: ${data.NoiVaoDoan} \n\n`,
                            `Ni vo ng ln u: ${data.NoiVaoDangLanDau} \n\n`,
                            `Ni vo ng chnh thc: ${data.NoiVaoDangChinhThuc} \n\n`,
                            `Ngi gii thiu: ${data.NguoiGioiThieu || ""}\n\n`,
                        ],
                        width: 210
                    }
                ],

            },
        ],
        styles: {
            header: {
                fontSize: 14,
                alignment: 'justify'
            },
            tableExample: {
                margin: [0, 5, 0, 15]
            },
            content: {
                margin: [0, 30, 0, 0],
            }
        }

    }

    const handleDownLoad = () => {
        pdfmakedownload(dd);
    }

    return (
        <>
            {
                button
                    ?
                    <MyButton onClick={handleDownLoad} style={{ marginLeft: 8 }} success>Xut h s</MyButton>
                    :
                    <div onClick={handleDownLoad} className={classes.iconWrapper} >
                        <FileDownloadOutlinedIcon className={classes.icon} />Xut h s
                    </div>
            }
        </>
    );
};

export default ExportFile;