import DeleteIcon from '@mui/icons-material/Delete';
import { Grid, Typography } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import makeStyles from '@mui/styles/makeStyles';
import React, { useEffect, useState } from 'react';
import image from '../public/image/warning.png';
import MyButton from './UI/MyButton';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { getLocaleDate } from '../utils/utils';
const useStyles = makeStyles(theme => ({
    dialogContent: {
        textAlign: 'center'
    },
    icon: {
        margin: theme.spacing(0.5, 1, 0.5, 0),
        fontSize: '1.2rem'
    },
    iconWrapper: {
        display: 'flex',
        alignItems: 'center',
        width: '100%',
        height: '100%',
    },
    deleteBtn: {
        color: theme.palette.common.white,
        backgroundColor: theme.palette.error.main,
        '&:hover': {
            backgroundColor: theme.palette.error.dark,
        }
    },
}))

const DetailForm = (props) => {

    const classes = useStyles();
    const { data, btn, noBtn, openForm, setOpenForm } = props;
    console.log(data);
    const { HinhAnh, HoTen, MaSoDangVien, CMND, TenGioiTinh, NgaySinh, NoiSinh, TenChiBo, TenChucVu,
        Email, SoDienThoai, TenDanToc, TenTonGiao, QueQuan, DiaChiThuongTru, NoiOHienTai,
        SoThe, TrinhDoHocVan, TenChinhTri, TenTinHoc, NgoaiNguTrinhDo,
        NgayVaoDoan, NoiVaoDoan, NgayChinhThuc, NgayVaoDang, NoiVaoDangLanDau, NoiVaoDangChinhThuc,
        NguoiGioiThieu
    } = data
    const [open, setOpen] = useState(false);
    const handleClose = () => {
        setOpen(false)
    }
    const handleOpen = () => {
        setOpen(true)
    }

    useEffect(() => {
        setOpen(!!openForm);
    }, [openForm])

    return (
        <>
            {
                !noBtn &&
                (btn
                    ?
                    <MyButton onClick={handleOpen} error><DeleteIcon /></MyButton>
                    :
                    <div className={classes.iconWrapper} onClick={handleOpen}>
                        <VisibilityIcon className={classes.icon} />Xem chi tit
                    </div>
                )
            }

            <Dialog PaperProps={{ style: { minWidth: '1000px' } }} fullWidth open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Chi tit</DialogTitle>
                <DialogContent >
                    <Grid container>
                        <Grid item xs={4}>
                            <img style={{ width: '150px', height: '200px' }} src={HinhAnh} alt="avatar"></img>
                        </Grid>
                        <Grid container item xs={4}>
                            <Grid item xs={5}>
                                <Typography>H tn</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>{HoTen}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    M ng vin</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {MaSoDangVien}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    CMND</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {CMND}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    Gii tnh</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TenGioiTinh}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    Ngy sinh</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>{getLocaleDate(NgaySinh)}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    Ni sinh</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {NoiSinh}</Typography>
                            </Grid>
                        </Grid>
                        <Grid item container xs={4}>
                            <Grid item xs={5}>
                                <Typography>    Chi b</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TenChiBo}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    Chc v</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TenChucVu}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    Email</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {Email}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    S in thoi</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {SoDienThoai}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    Dn tc</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TenDanToc}</Typography>
                            </Grid>
                            <Grid item xs={5}>
                                <Typography>    Tn gio</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TenTonGiao}</Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid><Typography>Qu qun: {QueQuan}</Typography></Grid>
                    <Grid><Typography>a ch thng tr: {DiaChiThuongTru}</Typography></Grid>
                    <Grid><Typography>Ni  hin ti: {NoiOHienTai}</Typography></Grid>
                    <Grid container>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    S th</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {SoThe}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Trnh  hc vn</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TrinhDoHocVan}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Trnh  tin hc</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TenTinHoc}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Trnh  chnh tr</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {TenChinhTri}</Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid><Typography>Ngoi ng - trnh : {NgoaiNguTrinhDo}</Typography></Grid>
                    <Grid container>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Ngy vo on</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {getLocaleDate(NgayVaoDoan)}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Ni vo on</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {NoiVaoDoan}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Ngy vo ng ln u</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {getLocaleDate(NgayVaoDang)}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Ni vo ng ln u</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {NoiVaoDangLanDau}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Ngy vo ng chnh thc</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {getLocaleDate(NgayChinhThuc)}</Typography>
                            </Grid>
                        </Grid>
                        <Grid container item xs={6}>
                            <Grid item xs={5}>
                                <Typography>    Ni vo ng chnh thc</Typography>
                            </Grid>
                            <Grid item xs={7}>
                                <Typography>    {NoiVaoDangChinhThuc}</Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid><Typography>Ngi gii thiu: {NguoiGioiThieu}</Typography></Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} >
                        Hy
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default DetailForm;