import ThumbDownAltIcon from '@mui/icons-material/ThumbDownAlt';
import ThumbUpAltIcon from '@mui/icons-material/ThumbUpAlt';
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle, FormControl,
    Grid, MenuItem
} from '@mui/material';
import makeStyles from '@mui/styles/makeStyles';
import React, { useContext, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { createRewardDiscipline } from '../action/rewardDisciplineAction';
import { SnackbarContext } from '../contextAPI/SnackbarContext';
import { getDate } from '../utils/utils';
import InputGrid from './InputGrid';
import MyButton from './UI/MyButton';

const useStyles = makeStyles(theme => ({
    icon: {
        margin: theme.spacing(0.5, 1, 0.5, 0),
        fontSize: '1.2rem'
    },
    iconWrapper: {
        display: 'flex',
        alignItems: 'center',
        width: '100%',
        height: '100%'
    },
    divider: {
        marginTop: '20px'
    },
}))

const RewardDisciplineForm = (props) => {
    const classes = useStyles();
    const { reward, dataSelect, btn, openForm, setOpenForm } = props
    const [open, setOpen] = useState(false);
    const { openSnackbar, openSnackbarDispatch } = useContext(SnackbarContext)

    const {
        register,
        handleSubmit,
        control,
        setValue,
        watch,
        clearErrors,
        formState: { errors }
    } = useForm();

    const handleClose = () => {
        setOpenForm && setOpenForm(false);
        setOpen(false)
    }
    const handleOpen = () => {
        setOpen(true)
    }
    const handleChangeSelect = (e) => {
        setValue(e.target.name, e.target.value)
    }
    const onSubmit = async (data) => {
        data.MaSoDangVienArr = dataSelect.map(el => ({ MaSoDangVien: el.MaSoDangVien }))
        const res = await createRewardDiscipline({ data, type: reward ? "reward" : "discipline" }, openSnackbarDispatch)
        if (res.error) {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: res.error.message,
                    type: "error"
                }
            })
        } else {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: res.msg,
                    type: "success"
                }
            })
        }
        setOpen(false);
    }

    useEffect(() => {
        setOpen(!!openForm);
    }, [openForm])

    useEffect(() => {
        if (dataSelect) {
            dataSelect.map((el, index) => {
                setValue(`MaSoDangVien${index}`, el.MaSoDangVien)
                setValue(`HoTen${index}`, el.HoTen)
            })
        }
    }, [])

    return (
        <>
            {btn &&
                <div className={classes.iconWrapper} onClick={handleOpen}>
                    {reward ?
                        <><ThumbUpAltIcon className={classes.icon} />Khen thng</>
                        :
                        <><ThumbDownAltIcon className={classes.icon} />K lut</>
                    }
                </div>
            }
            <Dialog PaperProps={{ style: { minWidth: '1000px' } }} open={open} onClose={handleClose}>
                <DialogTitle id="form-dialog-title">{reward ? "Khen thng" : "K lut"}</DialogTitle>
                <DialogContent>
                    <FormControl margin="dense" fullWidth>
                        <Grid container spacing={1}>
                            {dataSelect.map((el, index) =>
                                <React.Fragment key={index}>
                                    <Grid item xs={6}>
                                        <InputGrid
                                            nameTitle={`M s ng vin`}
                                            name={`MaSoDangVien${index}`}
                                            defaultValue={""}
                                            control={control}
                                            errors={errors}
                                            disabled
                                        />
                                    </Grid>
                                    <Grid item xs={6}>
                                        <InputGrid
                                            nameTitle={`H tn`}
                                            name={`HoTen${index}`}
                                            defaultValue={""}
                                            control={control}
                                            errors={errors}
                                            disabled
                                        />
                                    </Grid>
                                </React.Fragment>

                            )}

                            {reward ?
                                <Grid item xs={6}>
                                    <InputGrid
                                        select
                                        onChange={handleChangeSelect}
                                        nameTitle={"Hnh thc"}
                                        name={"HinhThuc"}
                                        defaultValue={"0"}
                                        rules={{
                                            validate: value =>
                                                value != "0" || "Vui lng nhp trng ny!"
                                        }}
                                        control={control}
                                        errors={errors}
                                    >
                                        <MenuItem value="0">Chn hnh thc</MenuItem>
                                        <MenuItem value="Biu dng">Biu dng</MenuItem>
                                        <MenuItem value="Tng giy khen">Tng giy khen</MenuItem>
                                    </InputGrid>
                                </Grid>
                                :
                                <Grid item xs={6}>
                                    <InputGrid
                                        select
                                        onChange={handleChangeSelect}
                                        nameTitle={"Hnh thc"}
                                        name={"HinhThuc"}
                                        defaultValue={"0"}
                                        rules={{
                                            validate: value =>
                                                value != "0" || "Vui lng nhp trng ny!"
                                        }}
                                        control={control}
                                        errors={errors}
                                    >
                                        <MenuItem value="0">Chn hnh thc</MenuItem>
                                        <MenuItem value="Khin trch">Khin trch</MenuItem>
                                        <MenuItem value="Cnh co">Cnh co</MenuItem>
                                        <MenuItem value="Cch chc">Cch chc</MenuItem>
                                        <MenuItem value="Khai tr">Khai tr</MenuItem>
                                    </InputGrid>
                                </Grid>
                            }
                            <Grid item xs={6}>
                                <InputGrid
                                    type="date"
                                    defaultValue={getDate(new Date)}
                                    nameTitle={reward ? "Ngy khen thng" : "Ngy k lut"}
                                    name={reward ? "NgayKhenThuong" : "NgayKyLuat"}
                                    control={control}
                                    errors={errors}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <InputGrid
                                    nameTitle={reward ? "Tn khen thng" : "Tn k lut"}
                                    name={reward ? "TenKhenThuong" : "TenKyLuat"}
                                    defaultValue={""}
                                    control={control}
                                    errors={errors}
                                />
                            </Grid>
                        </Grid>

                    </FormControl>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} >
                        Hy
                    </Button>
                    <MyButton onClick={handleSubmit(onSubmit)} info>
                        Lu
                    </MyButton>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default RewardDisciplineForm;