export const fileColumn = (rows, setRows, handleMailing, handleRemove, fetch) => {
    let col = infoColumns();
    col.push(
        {
            title: "Kch hot", field: 'mailing', align: 'center', maxWidth: 100,
            render: (params) =>
                params.DaXacNhan == 0 ?
                    <MailIcon onClick={(e) => handleMailing(e, params.Email)} color="warning" sx={{ mr: 1.25, cursor: 'pointer' }} />
                    :
                    <MarkEmailReadIcon color="success" sx={{ mr: 1.25 }} />
        },
        {
            title: "Chc nng", field: "action", sorting: false, maxWidth: 120,
            render: (params) => {
                return <ActionMenu
                    data={params}
                    rows={rows}
                    setRows={setRows}
                    fetch={fetch}
                    handleRemove={(e) => handleRemove(e, params.MaSoDangVien)}
                />
            }
        })
    return col
}

export const getExportData = (rows, columns) => {
    const headers = columns.map(el => ({ label: el.title, key: el.field })).filter(el => el.key != "action" && el.key != "mailing");
    const headerArr = columns.map(el => el.field);
    const newRows = rows.map(el => {
        let newEl = {};
        Object.keys(el).map(key => {
            if (headerArr.includes(key))
                newEl[key] = (dateArr.includes(key) && el[key]) ? getDate(el[key]) : el[key]
        })
        return newEl;
    })
    return { data: newRows, headers: headers }
}

